.aboutCrs__wrap {
  margin: auto;
  padding-left: 30px;
  padding-right: 30px;
  margin-top: 6px;
}

.aboutCrs__info {   
  max-width: 600px;
  margin: auto;
}

.aboutCrs__ttl {
  margin: 0;
  padding: 0;
  font-size: $h2-m;
  font-family: $font-1-l;
  color: $color-1;
}

.aboutCrs__txt {
  font-size: 14px;
  line-height: 20px;
  font-family: $font-1;
  padding: 0;
  margin: 0;
  margin-top: 19px;  
  color: $color-1;
}
.aboutCrs__txt2 {  
  margin-top: 16px;
}

.aboutCrs__time {
  padding: 0;
  margin: 0;
}

.aboutCrs__time {
  padding: 0;
  margin: 0;  
  max-width: 600px;
  margin: auto;
}

.aboutCrs__tm-item {
  list-style: none;
  margin-top: 30px;
  padding-bottom: 22px;
  &:not(:last-of-type) {
    border-bottom: 1px solid #d9dee4;
  }
}

.aboutCrs__tm-ttl {
  display: block;
  font-family: $font-1-b;
  font-size: 11px;
  letter-spacing: 1px;
  color: $color-1;
}

.aboutCrs__tm-txt {
  display: block;
  font-family: $font-1-l;
  font-size: 22px;
  color: $color-1;
  margin-top: 5px;
}


@media #{$tablet} {
  .aboutCrs__wrap {
    @include flex-row();
    padding-right: 0;
    padding-left: 0;
    width: $max-tablet;
    justify-content: space-between;    
    margin-top: 50px;
  }
  
  .aboutCrs__info {
    max-width: none;
    width: 411px;
    margin: 0;    
  }
  
  .aboutCrs__ttl {
    font-size: $h2-t;    
  }
  
  .aboutCrs__txt {
    font-size: 14px;
    margin-top: 25px;
    padding: 0;
  }
  .aboutCrs__txt2 {
    margin-top: 15px;
  }
  
  .aboutCrs__time {
    padding: 0;
    margin: 0;
    margin-top: 0px;
    margin-top: 15px;
  }
  
  .aboutCrs__tm-item {
    margin: 0;
    width: 302px;
    margin-bottom: 30px;
  }
  
  .aboutCrs__tm-ttl {
    font-size: 11px;
  }
  
  .aboutCrs__tm-txt {
    font-size: 22px;
  }
}


@media #{$desktop} {
  .aboutCrs__wrap {
    width: $max-desktop;
  }
  
  .aboutCrs__txt {
    width: 500px;
  }
  
  .aboutCrs__time {
    width: 370px;
  }
  
  .aboutCrs__tm-item {
    width: auto;
    margin-bottom: 31px;
  }
}