.footer {
  background: $color-7;
}

.footer__wrap {
  
}

.footer__items {
  max-width: 600px;  
  padding: 0;
  margin: 0;
  padding: 30px;
  padding-top: 25px;
  list-style: none;
  margin: auto;
}

.footer__item {
  @include flex();
}

.footer__txt {
  padding: 0;
  margin: 0;
}

.footer__item-h {
  display: none;
}

.footer__adress {
  @include flex();
}

.footer__ttl {
  font-family: $font-1;
  font-size: 14px;
  color: $color-6;
}

.footer__txt {
  font-family: $font-1;
  font-size: 14px;
  color: #fff;
  line-height: 20px;
  margin-top: 8px;
}

.footer__link {
  font-size: 14p;
  color: $color-6;
  text-decoration: none;
  font-size: 14px;
  &:hover {
    text-decoration: underline;
  }
}

.footer__ttl--mail {
  margin-top: 22px;
}

.footer__item--phone {
  margin-top: 20px;
}

.footer__phone {
  color: #fff;
  font-size: 16px;
  text-decoration: none;
  margin-top: 3px;
  font-family: $font-1;  
}

.footer__social {
  list-style: none;
  @include flex-row();
  padding: 0;
  margin-top: 33px;
  margin-left: 2px;
}

.footer__sc-item {
  margin-right: 9px;
}

.footer__sc-link {
  display: block;
  border-radius: 50%; 
}

.footer__sc-tw {  
  @include bg($tw);
}

.footer__sc-fc {
  @include bg($fc);
}

.footer__sc-yt {
  @include bg($yb);
}

.footer__sc-inst {
  @include bg($inst);
}

.footer__made-by {
  margin-top: 10px;
  border-top: 1px solid #30353d;
  padding: 30px;  
  font-family: $font-1;
  font-size: 12px;
  color: $color-6;  
}

@media #{$tablet} {
  .footer__wrap {
    
  }
  .footer__items {
    @include flex-row();
    width: $max-tablet;
    max-width: none;
    padding-top: 45px;
    padding-bottom: 30px;
  }
  
  .footer__item-h {
    display: block;
    width: 315px;
    margin-right: 13px;
  }
  
  .footer__logo {
    width: 231px;
    height: 58px;    
    margin-bottom: 10px;    
  }
  
  .footer__adress {
    width: 304px;
    margin-top: 8px;
  }
  
  .footer__item--phone {
    margin-left: auto;
    margin-right: 80px;
    margin-top: 14px;
  }
  
  .footer__social {
    margin-top: 50px;
  }
  
  .footer__made-by {    
    margin-top: 30px;
    padding-top: 45px;
    padding-bottom: 45px;
  }
  
  .footer__made-wrap {
    @include flex-row();
    justify-content: space-between;
  }
  
  .footer__jo-txt {
    font-family: $font-1;
    font-size: 12px;
    color: $color-6;
  }
  
  .footer__heart {
    display: inline-block;
    @include bg($heart);
    margin-left: 1px;
    margin-right: 45px;
  }
  
}


@media #{$desktop} {
  .footer__items {
    width: $max-desktop;
  }
  
  .footer__item-h {
    margin-right: 85px;
  }
  
  .footer__item--phone {
    margin-right: 130px;
  }
  
  .footer__made-wrap {
    width: $max-desktop;
    margin: auto;
  }
  
  .footer__heart {
    margin-right: 90px;
  }
}

