.buycrs {
  background: #f6f9fb;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 40px;
  margin-top: -16px;
}

.buycrs__wrap {
  padding: 20px;
  padding-bottom: 30px;
  max-width: 560px;
  background: #fff;
  margin: auto;
  box-shadow: 1px 2px 20px 0px rgba(0,0,0, 0.1); 
}

.buycrs__ttl {
  padding: 0;
  margin: 0;
  font-family: $font-1;
  font-size: 20px;
  line-height: 26px;
  color: $color-1;
  font-weight: normal;
  margin-top: 8px;
}

.buycrs__price {
  display: block;
  font-family: $font-1-b;
  font-size: 16px;
  color: $color-1;
  margin-top: 20px;
}

.buycrs__buttons {
  @include flex();
  align-items: center;
}

.buycrs__enroll {
  @include btn();
  margin-top: 21px;
  width: 216px;
}

.buycrs__call-back {
  @include btn();
  margin-top: 20px;
  width: 216px;
}

@media #{$tablet} {
  .buycrs {
    padding-bottom: 60px;
  }
  .buycrs__wrap {
    @include flex-row();    
    width: $max-tablet;
    max-width: none;
    padding: 0;
  }
  
  .buycrs__arrow {
    position: absolute;
    height: 40px;
    width: 40px;
    background: #fff;
    margin-left: 460px;
    transform: rotate(45deg);   
    margin-top: -8px;
  }
  
  .buycrs__info {
    padding: 40px;
    padding-top: 45px;  
  }
  
  .buycrs__ttl {
    font-size: $h3-t;
    width: 426px;
    margin: 0;
    padding: 0;
  }
  
  .buycrs__price {
    font-size: 18px;
    margin-top: 15px;
  }
  
  .buycrs__buttons {
    flex-direction: row;
    margin-right: 20px;   
    margin-top: 15px;
    margin-left: auto;
  }
  
  .buycrs__enroll,
  .buycrs__call-back {
    width: auto;
    margin-top: 0;    
    padding-left: 17px;
    padding-right: 17px;
    margin-right: 20px;
  }
}


@media #{$desktop} {
  .buycrs {
    padding-bottom: 90px;
  }
  .buycrs__wrap {
    width: $max-desktop;    
  }
  
  .buycrs__info {
    padding-left: 50px;
  }
  
  .buycrs__ttl {
    width: 550px;
  }
  
  .buycrs__buttons {
    margin-top: 10px;
    margin-right: 30px;
  }
  
  .buycrs__arrow {
    margin-left: 566px;
  }
  
}