.cours {
  background: $color-2;
}

.cours__wrap {
  @include flex();
  position: relative;
  padding-left: 30px;
  padding-right: 35px;
  padding-top: 27px;
}

.cours__mini-ttl {
  font-size: 12px;
  font-family: $font-1;  
  color: $color-4;
  font-weight: bold;  
}

.cours__title {
  font-size: $h1-m;
  font-family: $font-1-l;
  color: $color-1;
  font-weight: normal;
  padding: 0;
  margin: 0;
  margin-top: 10px;
  margin-left: -3px;
}

.cours__container {
  background: #fff;
}

.cours__slider {
  width: 319px;
  margin: auto;
  margin-top: 19px;
}

.cours__slider-items { 
  margin: 0;
  padding: 0;
  list-style: none;
}

.cours__slide-nav {  
  z-index: 100;  
  position: absolute;
  @include flex-row();  
  margin-top: -60px;
  margin-left: 110px;
  width: 110px;
  justify-content: space-between;
  & .active .cours__slide-dot {
    height: 9px;
    width: 9px;
  }
}

.cours__slide-ind {  
  @include flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  width: 30px;
  cursor: pointer;
}

.cours__slide-dot {
  height: 4px;
  width: 4px;
  background: #fff;
  border-radius: 50%;  
}

.cours__info {  
  @include flex();  
  display: block;
  align-items: center;
  padding: 30px;
  padding-top: 25px;
  margin: auto;
  max-width: 600px;  
}

.cours__txt {
  font-family: $font-1;
  padding: 0;
  margin: 0;
  font-size: 16px;
  color: $color-1;
  line-height: 24px; 
  max-width: 584px;
}

.cours__price {
  display: block;
  font-size: 16px;
  font-family: $font-1-b;
  color: $color-1;
  margin-top: 9px;
  align-self: flex-start;
}

.cours__enroll {  
  @include btn();
  margin-top: 24px;
}

.cours__call-back {
  @include btn();
  margin-top: 18px;
}

.cours__buttons {
  @include flex();
  align-items: center;
}


@media #{$tablet} {
  .cours__wrap {
    width: $max-tablet;
    padding-right: 0;
    padding-left: 0;
    padding-top: 47px;
    padding-bottom: 110px;
    margin: auto;
  }
  
  .cours__title {
    font-size: $h1-t;
    margin-top: 15px;
    max-width: 821px;
    line-height: 64px;
    padding-right: 30px;
  }

  .cours__container {
    @include flex();
    background: $color-8;
    padding-bottom: 80px;
  }

  .cours__cont-wrap {
    @include flex();
    flex-direction: row-reverse;
    background: #fff;
    position: relative;
    margin: auto;
    margin-top: -66px;
    width: $max-tablet; 
    justify-content: space-between;
    padding: 0;    
  }
  
  .cours__slider {
    width: 479px;
    margin: 0;
    overflow: hidden;
  }
  
  .cours__slide-nav {
    margin-left: 200px;
  }
  
  .cours__info {
    width: 480px;  
    padding: 0;
    padding-top: 51px;
    margin-top: 0;
    
  }
  
  .cours__txt {
    padding-left: 40px;
    padding-right: 40px;
    font-size: 18px;
    line-height: 28px;
  }
  
  .cours__price {
    padding-left: 40px;
    padding-right: 40px;
    font-size: 18px;
    margin-top: 23px;
  }
  
  .cours__buttons {
    @include flex-row();
    margin-top: 30px;
    border-top: 1px solid $color-9;
    width: 100%;
    padding-top: 38px;
    margin-left: 40px;
  }
  
  .cours__enroll,
  .cours__call-back {
    width: auto;
    margin: 0; 
    margin-left: 20px;
    padding-left: 16px;
    padding-right: 16px;
  }  
    
}

@media #{$desktop} {
  .cours__wrap {
    width: $max-desktop;
  }
  
  .cours__title {
    max-width: none;    
  }
  
  .cours__cont-wrap {
    width: $max-desktop;
  }
  
  .cours__slider {
    width: 585px;
  }
  
  .cours__slide-nav {
    margin-left: 253px;
  }
  
  .cours__info {
    width: 584px;
  }
  
  .cours__txt {
    padding-left: 50px;
    padding-right: 75px;
  }
  
  .cours__price {
    padding-left: 50px;
  }
  
  .cours__buttons {
    margin: 0;
    margin-top: 60px;    
  }
  
  .cours__enroll {
    margin-left: 50px;
  }
  
}










