/* Your custom fonts here */
@include font-face('LatoRegular', '../fonts/Lato-Regular', normal, italic, $exts: woff woff2);
@include font-face('LatoLight', '../fonts/Lato-Light', normal, italic, $exts: woff woff2);
@include font-face('LatoBlack', '../fonts/Lato-Black', normal, italic, $exts: woff woff2);



$font-1: LatoRegular, Helvetica, Arial, sans-serif;
$font-1-l: LatoLight , Helvetica, Arial, sans-serif;
$font-1-b: LatoBlack , Helvetica, Arial, sans-serif;

$h1-m: 28px;
$h2-m: 24px;
$h3-m: 20px;

$h1-t: 48px;
$h2-t: 32px;
$h3-t: 24px;




