.program {
  background: #f6f9fb;
  margin-top: 40px; 
  padding-bottom: 20px;
}

.program__wrap {
  max-width: 600px;
  padding-left: 30px;
  padding-right: 30px;  
  margin: auto;
}

.program__ttl {
  padding-top: 32px;
  font-size: $h2-m;
  font-family: $font-1-l;
  color: $color-1;
  font-weight: normal;
  text-align: center;
  margin: 0;
}

.program__items {
  padding: 0;
  margin-top: 28px;
}

.program__item {
  display: none;
}

.program__info {
  background: #fff;
  margin-bottom: 18px;
  box-shadow: 1px 2px 20px 0px rgba(0,0,0, 0.1); 
}

.program__info-ttl {
  font-size: $h3-m;
  color: $color-1;
  font-family: $font-1;
  font-weight: normal;
  padding-top: 27px;
  padding-left: 20px;
  margin: 0;
}

.program__info-itms {
  padding: 0;
  list-style: none;
  padding-left: 20px;
  padding-right: 20px;
  margin-top: -3px;
  padding-bottom: 35px;  
}

.program__info-itm {
  @include flex-row();
  font-size: 14px;
  font-family: $font-1;
  margin-top: 10.5px;
  line-height: 20px;
  &:before {
    display: inline-block;    
    content: '';
    height: 5px;
    width: 5px;
    border-radius: 50%;
    background: $color-1;
    margin-right: 13px;    
    margin-top: 6px;
    box-sizing: border-box;
    border: 3px solid $color-1;
  }
}

.program__info-ttl-h,
.program__info-itms-h {
  display: none;
}


@media #{$tablet} {
  .program {
    margin-top: 50px;
    padding-bottom: 75px;
  }
  .program__wrap {
    position: relative;
    width: $max-tablet;    
    max-width: none;
  }
  
  .program__ttl {
    font-size: 36px;
    padding-top: 75px;
  }
  
  .program__items {
    margin-top: 100px;
  }
  
  .program__item-wrap {
    @include flex-row();
    justify-content: space-between;  
    margin-bottom: 60px;
    &:nth-of-type(2),
    &:nth-of-type(4),
    &:nth-of-type(6) {
      flex-direction: row-reverse;
    }    
  }   
  
  .program__item {
    @include flex();
    justify-content: center;
    align-items: center;
  }
  
  .program__info-itms-h,
  .program__info-ttl-h {
    display: block;
  }
  
  .program__info {
    width: 442px;   
    margin: 0;
    padding-top: 15px;
  }
  
  .program__info-ttl {
    font-size: $h3-t;
    padding-left: 40px;
    margin-bottom: 22px;
  }
  
  .program__info-itm {    
    padding-left: 20px;    
    padding-right: 20px;
  }
  
  .program__line {    
    position: absolute;
    width: 2px;
    background: #d9dee4;
    height: 2987px;
    left: 50%;
    top: 152px;
    &:before {
      content: '';
      display: inline-block;
      height: 13px;
      width: 13px;
      background: #d9dee4;
      border-radius: 50%;
      margin-left: -6px;      
      margin-bottom: 10px;
      box-shadow: 0px 0px 0px 2px $color-8;
    }
    &:after {
      content: '';
      display: inline-block;
      height: 13px;
      width: 13px;
      background: #d9dee4;
      border-radius: 50%;
      margin-left: -6px;   
      margin-top: 2949px;
      box-shadow: 0px 0px 0px 2px $color-8;
    }
  }
  
  .program__dots {    
    @include flex-row();    
    align-items: center;
    position: absolute;
    width: 70px;
    height: 50px;
    margin-left: -43px;    
    margin-top: 20px;    
  }
  
  .program__dots-2,
  .program__dots-4,
  .program__dots-6 {
    flex-direction: row-reverse;
    margin-left: 417px; 
  }
  
  .program__dot {
    height: 8px;
    width: 8px;
    background:$color-8;
    border: 2px solid #d9dee4;
    border-radius: 50%;
    box-shadow: 0px 0px 0px 2px $color-8;
  }
  
  .program__item-wrap:nth-of-type(2n) .program__arrow {
    margin: 0;
    margin-right: 23px;
  }
  
  .program__arrow {
    height: 40px;
    width: 40px;
    background: #fff;
    margin-left: 23px;
    transform: rotate(45deg);     
  }
}

@media #{$desktop} {
  .program {
    margin-top: 30px;
  }
  
  .program__wrap {
    width: $max-desktop;
  }
  
  .program__info {
    width: 549px;
  }
  
  .program__info-ttl {
    padding-left: 50px;
  }
  
  .program__item picture {
    @include flex;
  }
  
  .program__info-itms {
    padding-left: 30px;
    padding-right: 70px;
  }
  
  .program__dots {
    margin-left: -40.1px;
  }
  
  .program__dots-2,
  .program__dots-4,
  .program__dots-6 {
    flex-direction: row-reverse;
    margin-left: 522.1px; 
  }
  
}
















