.header {
  background: $color-2;  
}

.header__wrap {
  padding: 30px;
  padding-top: 20px;
  padding-bottom: 0;
}

.header__logo {
  @include flex-row();    
  align-items: center;  
  justify-content: space-between;
}

.header__logo-img {
  width: 185px;
  height: 45px;
  margin-right: 45px;
}

.header__menu-btn {
  height: 30px;
  width: 30px;
}

.header__menu-btn-line {  
  background: $color-1;
  height: 2px;
  width: 31px;  
  &:before {
    position: absolute;
    background: $color-1;
    display: block;
    content: '';
    height: 2px;
    width: 31px;
    margin-top: 10px;    
  }
  &:after {
    position: absolute;
    background: $color-1;
    display: block;
    content: '';
    height: 2px;
    width: 31px;
    margin-top: 20px;
  }
}

.header__menu {
  padding: 0;
  margin: 0;
  border-top: 1px solid #fff;
  display: none;  
}

.header__menu-item {
  list-style: none;
  width: 100%;
}

.header__menu-link {
  display: block;
  text-decoration: none;
  color: $color-1;
  padding: 12px;
  text-align: center;
  border-bottom: 1px solid #fff;
  transition: 0.2s ease;
  font-family: $font-1;
  font-size: 16px;
  &:hover {
    color: $color-3;
    transition: 0.2s ease;
  }
}

.header__search {
  display: block;
  margin: auto;
  padding: 10px;
  margin-top: 10px;
}

.header__to-enroll {
  display: none;
}


@media #{$tablet} {
  .header__wrap {
    @include nl();
    @include flex-row();
    padding-right: 0;
    padding-left: 0;
    padding-top: 35px;
    width: $max-tablet;
    margin: auto;
  }
  
  .header__logo-link {
    display: block;
    width: 204px;
  }
  
  .header__logo {
    display: block;
    margin-right: 45px;
  }
  
  .header__logo-img {
    width: 204px;
    height: 47px;
  }
  
  .header__menu-btn {
    display: none;
  }
  
  .header__menu {
    display: block;
    border: none;
    width: 575px;
    padding-top: 10px;
  }
  
  .header__menu-item {
    display: inline-block;
    width: auto;
    margin-right: 25px;
    vertical-align: middle;
    &:nth-of-type(5n) {
      margin-right: 0;
    }
  }
  
  .header__menu-link {
    display: inline-block;
    padding: 0;
    border: none;
    font-size: 14px;      
  }  
  
  .header__menu-link--arrow {
    cursor: pointer;
    margin-bottom: 2px;
    display: inline-block;
    @include bg($header__arrow);
    margin-left: 10px;
    &:hover {
      @include bg($header__arrow-active);
    }
  }
  
  .header__menu-active {
    color: #a4a9af;
  }
  
  .header__link--arrow-active {
    @include bg($header__arrow-active);
  }

  .header__menu-search {
    cursor: pointer;
    display: inline-block;
    @include bg($search);
    margin: 0;
    margin-left: 35px;
  }

  .header__search {
    display: none;
  }

  .header__to-enroll {
    @include btn();
    @include flex-row();
    justify-content: center;
    align-items: center;
    width: 110px;  
    height: 10px;
    padding-top: 15px;
    padding-bottom: 15px;
    margin-left: auto;
    background: $color-2;
    & b {
      display: none;
    }
  }
}


@media #{$desktop} {
  .header__wrap {
    width: 1170px;
  }
  
  .header__logo {
    margin-right: 98px;
  }
  
  .header__to-enroll {
    width: 170px;
    & b {
      display: inline-block;
      font-weight: normal;
    }
  }
}
