.otherCrs {
  background: $color-2;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 20px;
}

.otherCrs__wrap {
  max-width: 600px;  
  padding-top: 35px;
  margin: auto;
}

.otherCrs__ttl {
  font-family: $font-1-l;
  font-size: $h2-m;
  color: $color-1;
  padding: 0;
  margin: 0;
  font-weight: normal;
  line-height: 32px;
}

.otherCrs__items {
  @include flex();
  align-items: center;
  margin-top: 15px;
}

.otherCrs__item {
  @include flex();
  background: #fff;  
  text-decoration: none;  
  padding: 30px;
  padding-top: 25px;
  margin-bottom: 20px;
  min-width: 200px;
  height: 115px;
  overflow: auto;
  width: 200px;
}

.otherCrs__txt {  
  font-family: $font-1;
  font-size: 14px;
  color: $color-1;  
}

.otherCrs__arrow {
  display: block;
  margin-top: auto;
  @include bg($ather__arrow);  
}

.otherCrs__item-h {
  display: none;
}

.otherCrs__show-more {
  display: none;
}

@media #{$tablet} {
  .otherCrs__wrap {
    width: $max-tablet;
    max-width: none;
    padding-top: 60px;
  }
  
  .otherCrs__ttl {
    font-size: $h2-t;
  }
  
  .otherCrs__items {
    flex-direction: row;
    flex-wrap: wrap;
    margin-top: 29px;
  }
  
  .otherCrs__item {
    margin-right: 24px;
    width: 244px;
    height: 165px;
    &:nth-of-type(3n) {
      margin-right: 0;
    }
  }
  
  .otherCrs__txt {
    width: 220px;
    
  }
  
  .otherCrs__item-h {
    display: flex;    
  }
  
  .otherCrs__item-t-h {
    display: none;
  }
  
  .otherCrs__show-more {
    cursor: pointer;
    @include btn();
    width: 132px;
    margin: auto;
    margin-top: 34px;
    background: $color-2;
    margin-bottom: 30px;
  }
}


@media #{$desktop} {
  .otherCrs {
    
  }
  .otherCrs__wrap {
    width: $max-desktop;
  }
  
  .otherCrs__item {
    margin-top: -5px;
    width: 210px;
    margin-right: 30px;
    &:nth-of-type(3n) {
      margin-right: 30px;
    }
    &:nth-of-type(4n) {
      margin-right: 0px;
    }
  }
  
  .otherCrs__item-d {
    display: flex;
  }
  
  .otherCrs__txt {
    line-height: 20px;
  }
}